.header-color {
  background-color: transparent;
}

.landing-grid {
  
  text-align: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.projects-grid {
  display: flex;
}

.my-img-landing {
  height: 250px;
  padding-top: 10em;
}

.banner-text {
  background-color: black;
  opacity: .8;
  width: 50%;
  margin: auto;
  border-radius: 20px;

}

.banner-text h1 {
  font-size: 60px;
  font-weight: bold;
  color: white;

}

.banner-text h2 {
  font-size: 25px;
  font-weight: bold;
  color: white;

}

.banner-text hr {
  border-top: 5px dotted white;
  width: 50%;
  margin: auto;

}

.social-links {
  display: flex;
  justify-content: space-between;
  width: 50%;
  margin: auto;
}

.social-links i {
  color: white;
  font-size: 5em;
  padding: 8px;
}

.contact-body {
  margin: auto;
  position: fixed;
  width: 100%;
  height: 100%;
  justify-content: center;
  padding-top: 2em;

}

.contact-grid {
  text-align: center;
  justify-content: center;
  width: 95%;
  height: 550px;
  background: white;
  color: black;
  margin-bottom: 20px;
}

.contact-grid h2 {
  font-family: 'Anton', sans-serif;
}

.contact-grid p {
  font-family: 'Oxygen', sans-serif; 
  font-weight: bold; 
}

.contact-grid hr {
  border-top: 5px dotted black;
  width: 50%;
  margin: auto;
}

.contact-list i {
  font-size: 66px;
  padding-right: 5rem;
}

.contact-grid Button {
  width: 200px;
  height: 75px;
  font-size: 20px;
  padding-top: 20px;
}

.about i {
  font-size: 10px;

}

.grid-head {
  font-size: 20px;
  display: flex;
  padding: 1em;
  
}

#tsparticles canvas {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: -1;
  background: linear-gradient(to left, #DECBA4, #3E5151);
}